  border-radius: 4px;
  margin-top: 10px;
  transition: transform 0.2s;
}
.remove-news .news-sidebar-item {
  display: none;
}
.remove-news.hidden + .add-empty-news {
  display: block;
}
.remove-news.active {
  transform: scale(1.2);
}
.news-template-group {
  padding: 8px 0;
  cursor: pointer;
}
.news-template-tab > .tab-pane > ul {
  list-style: none;
  padding: 0;
}



